import React from 'react'
import { Select } from 'antd'
import { Wrapper, DropdownHeader } from './dropdown.styles'
import { validate as isUuid } from 'uuid'

const { Option } = Select

const Dropdown = ({ title, style, options, handleClick, selected }) => {
    const handleChange = (val) => {
        handleClick(isUuid(val) ? { assetId: val } : val)
    }

    if (!options || !options.filter((el) => !el.disabled).length) return null

    return (
        <Wrapper>
            {title && <DropdownHeader>{title}</DropdownHeader>}
            <Select
                style={Object.assign({ width: 220 }, style)}
                value={selected}
                onChange={handleChange}
            >
                {options.map((option, i) => {
                    if (option.disabled) return null
                    let value
                    if (option.type === 'Asset') {
                        value = option.assetId
                    } else value = option.value
                    return (
                        <Option key={i} value={value}>
                            {option.name}
                        </Option>
                    )
                })}
            </Select>
        </Wrapper>
    )
}

export default Dropdown
