import { getConnection } from '../api/connect'
import products from '../api/products'
import { validate as isUuid } from 'uuid'

let PRODUCTS

const fetchProducts = () =>
    new Promise(async (resolve) => {
        if (PRODUCTS) return resolve(PRODUCTS)

        let itemsArr
        try {
            itemsArr = await products.find()
            if (!itemsArr) return reject('no items')
            PRODUCTS = itemsArr.reduce(
                (output, item) => Object.assign(output, { [item.id]: item }),
                {}
            )
            resolve(PRODUCTS)
        } catch (e) {
            reject(e)
        }
    })

const createThreekitScript = (options, callback) => {
    const script = document.createElement('script')
    script.src = options.bundleUrl?.length
        ? options.bundleUrl
        : `${
              options.threekitEnv?.length
                  ? `${options.threekitEnv}`
                  : 'admin.threekit.com'
          }/app/js/threekit-player-bundle.js`
    script.id = 'threekit-player-bundle'

    script.onload = () => callback()

    return script
}

const createPlayerLoader = () => {
    const playerElement = document.createElement('div')
    playerElement.setAttribute('id', 'player-root')
    playerElement.style.height = '100%'

    const playerLoader = document.createElement('div')
    playerLoader.appendChild(playerElement)
    playerLoader.style.opacity = '0'

    return playerLoader
}

const initializePlayer = (initializationObj) =>
    new Promise(async (resolve) => {
        const el = document.getElementById(
            initializationObj.elementId?.length
                ? initializationObj.elementId
                : 'player-root'
        )
        if (!el) return
        const api = await window.threekitPlayer({
            el,
            authToken: initializationObj.authToken,
            assetId: initializationObj.assetId,
            locale: "IT",
        })
        const configurator = await api.getConfigurator()

        window.threekit = { api, configurator }
        resolve({ api, configurator })
    })

export const loadPlayer = (initializationObj, callback) => {
    if (window.threekit) return

    const connectionObj = getConnection()
    if (!connectionObj) {
        throw new Error('Please connect to threekit')
    }

    let callbackArgs

    if (typeof initializationObj === 'string')
        callbackArgs = {
            authToken: connectionObj.authToken,
            assetId: initializationObj,
        }
    else
        callbackArgs = callbackArgs = {
            authToken: connectionObj.authToken,
            ...initializationObj,
        }

    const scriptOnLoad = async () => {
        await initializePlayer(callbackArgs)
        callback && callback()
    }

    const scriptEl = createThreekitScript(
        {
            bundleUrl:
                typeof initializationObj === 'string'
                    ? undefined
                    : initializationObj.bundleUrl,
            threekitEnv: connectionObj.threekitEnv,
        },
        scriptOnLoad
    )
    const playerLoader = createPlayerLoader()

    document.body.appendChild(playerLoader)
    document.head.appendChild(scriptEl)
}

export const addPlayerToComponent = (moveToElementId) => {
    const addPlayer = (tryCount = 0) => {
        if (tryCount >= 4) return

        let player = document.getElementById('player-root')
        if (!player)
            return setTimeout(() => {
                addPlayer(tryCount + 1)
            }, 0.05 * 1000)

        const playerWrapper = document.getElementById(moveToElementId)
        if (!playerWrapper) throw new Error('Player element not found')

        playerWrapper.appendChild(player)
    }

    addPlayer()
}

const hydrateAsset = (attribute, configuration) => {
    let hiddenValues
    if (attribute.hiddenValues?.length) {
        hiddenValues = new Set(
            attribute.hiddenValues.filter((el) => isUuid(el))
        )
    }

    const values = attribute.values.map((el) => ({
        ...el,
        type: 'Asset',
        metadata: PRODUCTS[el.assetId]?.metadata,
        tags: PRODUCTS[el.assetId]?.tags,
        disabled: hiddenValues && hiddenValues.has(el.assetId),
    }))

    return {
        [attribute.name]: {
            ...attribute,
            values,
            selected: configuration[attribute.name],
        },
    }
}

const hydrateString = (attribute, configuration) => {
    let hiddenValues
    if (attribute.disabledValues?.length) {
        hiddenValues = new Set(attribute.disabledValues)
    }

    const values = attribute.values.map((el) => ({
        name: el,
        value: el,
        disabled: hiddenValues && hiddenValues.has(el),
    }))

    return {
        [attribute.name]: {
            ...attribute,
            values,
            selected: configuration[attribute.name],
        },
    }
}

const hydrateAttribute = (attribute, configuration) => {
    switch (attribute.type) {
        case 'String':
            return hydrateString(attribute, configuration)
        case 'Asset':
            return hydrateAsset(attribute, configuration)
        default:
            return {
                [attribute.name]: {
                    ...attribute,
                    selected: configuration[attribute.name],
                },
            }
    }
}

export const getHydratedConfigurator = () =>
    new Promise(async (resolve, reject) => {
        if (!window.threekit) {
            setTimeout(async () => {
                const output = await getHydratedConfigurator()
                resolve(output)
            }, 0.2 * 1000)
            return
        }

        await fetchProducts()
        const attributes = await window.threekit.configurator.getAttributes()
        const configuration = await window.threekit.configurator.getConfiguration()

        const prepped = attributes.reduce(
            (output, attribute) =>
                Object.assign(
                    output,
                    hydrateAttribute(attribute, configuration)
                ),
            {}
        )

        resolve(prepped)
    })

export const getConfiguration = (attribute) =>
    new Promise(async (resolve) => {
        const configuration = await window.threekit.configurator.getConfiguration()
        const prepped = Object.entries(configuration).reduce(
            (output, [attribute, selection]) => {
                if (typeof selection === 'object' && selection.assetId) {
                    return Object.assign(output, {
                        [attribute]: {
                            ...selection,
                            metadata: PRODUCTS[selection.assetId].metadata,
                        },
                    })
                }

                return Object.assign(output, { [attribute]: selection })
            },
            {}
        )
        resolve(attribute ? prepped[attribute] : attribute)
    })

export const getBom = () =>
    new Promise(async (resolve) => {
        const configuration = await window.threekit.configurator.getConfiguration()
        const bom = Object.entries(configuration).reduce(
            (output, [attribute, selection]) => {
                if (typeof selection === 'object' && selection.assetId) {
                    return Object.assign(output, {
                        [attribute]: {
                            ...selection,
                            metadata: PRODUCTS[selection.assetId].metadata,
                        },
                    })
                }

                return Object.assign(output, { [attribute]: selection })
            },
            {}
        )
        resolve({ bom, configuration })
    })
