import threekit from '../api'
import { createSlice } from '@reduxjs/toolkit'
import { createSelector } from 'reselect'

const initialState = {
    loaded: false,
    attributes: undefined,
}

const { actions, reducer } = createSlice({
    name: 'threekit',
    initialState,
    reducers: {
        setLoaded: (state, _) => {
            state.loaded = true
        },
        setAttributes: (state, action) => {
            state.attributes = action.payload
        },
    },
})

export const { setLoaded, setAttributes } = actions

export const getAttribute = (attribute) =>
    createSelector(
        (state) => state.threekit.attributes,
        (attributes) => {
            if (!attributes) return undefined
            if (!attribute || !attributes[attribute]) return attributes
            return attributes[attribute]
        }
    )

export const setConfiguration = (config) => async (dispatch) => {
    config = getUpdatedConfiguration(config);//to invalid the selction. Need to check if user has called this function to set the configuration based on saved config ID.
    await window.threekit.configurator.setConfiguration(config)
    const attributes = await threekit.getHydratedConfigurator()
    dispatch(setAttributes(attributes))
}

const getUpdatedConfiguration = (config) => {
    console.log("config", config);
    var attributes = Object.keys(config);

    if(attributes.length == 1){
        switch (attributes[0]) {
            case "Design":
                config["Material"] = { assetId: "" };
                config["Finish"] = { assetId: "" };
                config["Product"] = { assetId: "" };
                config["Top"] = { assetId: "" };
                config["Structure"] = { assetId: "" };
                break;
    
            case "Material":
                config["Finish"] = { assetId: "" };
                config["Product"] = { assetId: "" };
                config["Top"] = { assetId: "" };
                config["Structure"] = { assetId: "" };
                break;
    
            case "Finish":
                config["Top"] = { assetId: "" };
                config["Structure"] = { assetId: "" };
                break;
    
            case "Product":
                config["Top"] = { assetId: "" };
                config["Structure"] = { assetId: "" };
                break;
        }
    }


    return config;
}
export const initialize = (config) => (dispatch) => {
    threekit.connect({
        orgId: config.orgId,
        authToken: config.authToken,
        threekitEnv: config.threekitEnv,
    })

    threekit.loadPlayer(config.assetId)

    threekit.getHydratedConfigurator().then((data) => {
        dispatch(setLoaded(true))
        dispatch(setAttributes(data))
    })
}

export default reducer
