import React from 'react'
import {
    Wrapper,
    ItemWrapper,
    SwatchWrapper,
    SwatchHeader,
    SwatchBody,
} from './swatch.styles'

const Item = (props) => {
    
    const { selected, onClick: handleClick } = props
        return (
        <ItemWrapper
            className={[
                'swatch-item-wrapper',
                selected ? 'swatch-item-selected' : '',
            ].join(' ')}
            onClick={handleClick}
            color={props.type !== 'Asset' ? props.color : undefined}
            selected={selected}
        >
            <div>
                <div className="swatch-item-img-wrapper">
                    {props.imgUrl && (
                        <img
                            className="swatch-item-img"
                            src={props.baseURL+ props.imgUrl}
                            alt={props.name}
                            title={props.name}
                        />
                    )}
                </div>
                {/* {props.name && (
                    <div className="swatch-item-label">{props.name}</div>
                )}  */}
            </div>
        </ItemWrapper>
    )
}

export const Swatch = ({
    title,
    options,
    handleClick,
    selected,
    imgFromMetadata,
    colorFromMetadata,
    baseURL,
}) => {
    if (!options || !options.filter((el) => !el.disabled).length) return null
    
    return (
        <SwatchWrapper>
            {title && <SwatchHeader>{title}</SwatchHeader>}
            <SwatchBody>
                <Wrapper>
                    {options.map((option, i) => {
                        if (option.disabled) return null
                        let value
                        if (option.type === 'Asset') {
                            value = option.assetId
                        } else value = option.value
                        
                        const propsFromMetadata = Object.assign(
                            {},
                            imgFromMetadata
                                ? { imgUrl: option.metadata[imgFromMetadata] }
                                : {},
                            colorFromMetadata
                                ? { color: option.metadata[colorFromMetadata] }
                                : {}
                        )
                        return (
                            <Item
                                key={i}
                                baseURL= {baseURL}
                                {...option}
                                {...propsFromMetadata}
                                selected={value === selected}
                                onClick={() =>
                                    handleClick(
                                        option.type === 'Asset'
                                            ? { assetId: option.assetId }
                                            : value
                                    )
                                }
                            />
                        )
                    })}
                </Wrapper>
            </SwatchBody>
        </SwatchWrapper>
    )
}

export default Swatch
