import React from 'react'
import { Player, InputComponents } from './components'
import threekit from './api'
import { Wrapper } from './app.styles'
import { Button } from 'antd'
import { setConfiguration } from './store/threekit'
const { Swatch, RadioButtons, Dropdown } = InputComponents
import { useSelector, useDispatch } from 'react-redux'


const App = (props) => {

    const dispatch = useDispatch()

    const getCod = (bom, a) => {
        if (bom[a]['metadata']) return bom[a]['metadata']['COD']
        return 'ERROR'
    }

    const getSKU = (bom, a) => {
        if (bom[a]['metadata']) return bom[a]['metadata']['sku']
        return 'ERROR'
    }

    const handleAddToCart = async () => {
        const { bom, configuration } = await threekit.getBom()

        if (getCod(bom, 'Material') === 'V') {
            //Lacquered Glass
            var sCOD =
                '1FR' +
                getCod(bom, 'Design') +
                getCod(bom, 'Material') +
                getCod(bom, 'Finish') +
                getCod(bom, 'Product')
        } else {
            var sCOD =
                '1FR' +
                getCod(bom, 'Design') +
                getCod(bom, 'Material') +
                getCod(bom, 'Top') +
                getCod(bom, 'Product')
        }

        var d = {} //for output json

        var skuTop = getSKU(bom, 'Top')
        if (!skuTop.includes('ERROR')) {
            var a1 = skuTop.split(' ')[0]
            var a2 = skuTop.split(' ')[1]
            d[a1] = a2
        }

        var skuStruct = getSKU(bom, 'Structure')
        if (!skuStruct.includes('ERROR')) {
            var b1 = skuStruct.split(' ')[0]
            var b2 = skuStruct.split(' ')[1]
            d[b1] = b2
        }
        var blnResult = true
        if (
            skuTop.includes('ERROR') ||
            skuStruct.includes('ERROR') ||
            sCOD.includes('ERROR')
        ) {
            //Error
            blnResult = false
            d = {}
            sCOD = ''
        }

        var res = {
            result: blnResult,
            sku: sCOD,
            data: d,
        }
        return res
    }



    const handleSaveConfig = async () => {
        const { bom, configuration } = await threekit.getBom()
        const config = {
            assetId: props.config.assetId,
            customerId: '',
            configuration: configuration,
            metadata: {
                SKU_TOP: bom['Top']['metadata']['sku'],
                SKU_STRUCTURE: bom['Structure']['metadata']['sku'],
            },
            productVersion: 'v1',
            thumbnail: '',
        }
        threekit.configurations.saveConfiguration(config).then(
            (val) => {
                //console.log(val)
                //console.log(val['id'])
            },
            (err) => {
                console.log(err)
            }
        )
    }

    window.getSKU = handleAddToCart
    window.saveConfig = handleSaveConfig
    return (
        <Wrapper>
            <Player />
            <div>
                <RadioButtons
                    title="Design"
                    attribute="Design"
                    imgFromMetadata="imageURL"
                />
                <RadioButtons title="Material" attribute="Material" />
                <RadioButtons title="Finish" attribute="Finish" />
                <RadioButtons title="Product" attribute="Product" />
                <Swatch
                    title="Top"
                    attribute="Top"
                    imgFromMetadata="imageURL"
                    baseURL={props.config.baseURL}
                />
                <Swatch
                    title="Structure"
                    attribute="Structure"
                    imgFromMetadata="imageURL"
                    baseURL={props.config.baseURL}
                />
                <Button type="primary" onClick={handleSaveConfig}>
                    Save Config
                </Button>
                
            </div>
        </Wrapper>
    )
}

export default App
