import React from 'react'
import {
    Wrapper,
    RadioButtonsHeader,
    Buttons,
    ButtonWrapper,
} from './radioButtons.styles'

const RadioButtons = ({ title, options, selected, handleClick }) => {
    if (!options || !options.filter((el) => !el.disabled).length) return null

    return (
        <Wrapper>
            {title && <RadioButtonsHeader>{title}</RadioButtonsHeader>}
            <Buttons>
                {options.map((option, i) => {
                    if (option.disabled) return null
                    let value
                    if (option.type === 'Asset') {
                        value = option.assetId
                    } else value = option.value
                    return (
                        <ButtonWrapper
                            key={i}
                            selected={value === selected}
                            onClick={() =>
                                handleClick(
                                    option.type === 'Asset'
                                        ? { assetId: option.assetId }
                                        : value
                                )
                            }
                        >
                            <div>{option.name}</div>
                        </ButtonWrapper>
                    )
                })}
            </Buttons>
        </Wrapper>
    )
}

export default RadioButtons
