import React from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { getAttribute, setConfiguration } from '../../store/threekit'

const container = (WrappedComponent, options) => (props) => {
    const { attribute } = props
    const dispatch = useDispatch()
    const attributeData = useSelector(getAttribute(attribute))

    const handleClick = (value) =>
        dispatch(setConfiguration({ [attribute]: value }))

    if (!attributeData) return null
    
    const passedProps = Object.assign(
        {},
        { title: props.title },
        {baseURL: props.baseURL},
        props.imgFromMetadata ? { imgFromMetadata: props.imgFromMetadata } : {},
        props.colorFromMetadata
            ? { colorFromMetadata: props.colorFromMetadata }
            : {}
    )

    return (
        <WrappedComponent
            {...passedProps}
            selected={attributeData.selected?.assetId || attributeData.selected}
            handleClick={handleClick}
            options={attributeData?.values}
        />
    )
}

export default container
