import React, { useEffect } from 'react'
import threekit from '../../api'

const Player = () => {
    useEffect(() => {
        ;(() => {
            threekit.addPlayerToComponent('threekit-player')
        })()
        return
    }, [])

    return <div style={{ height: '600px' }} id="threekit-player" />
}

export default Player
